import React, { createContext, useContext, useState, useEffect, ReactNode } from 'react';
import { User, AuthContextType, RegisterData, CINVerificationData } from '../types'; // Retirer CINVerificationResult
import { apiGet, apiJson, apiUpload, setAuthToken } from '../config';
import toast from 'react-hot-toast';
import { cinVerificationService } from '../services/cinVerificationService';
import { getStorage, removeStorage, clearStorage } from '../utils/storage';

const AuthContext = createContext<AuthContextType | undefined>(undefined);

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
};

interface AuthProviderProps {
  children: ReactNode;
}

export const AuthProvider: React.FC<AuthProviderProps> = ({ children }) => {
  const [user, setUser] = useState<User | null>(null);
  const [isLoading, setIsLoading] = useState(true);

 useEffect(() => {
  const token = getStorage('auth_token'); // Utiliser getStorage
  console.log(' Token rcupr pour le port', window.location.port, ':', token);
  
  if (!token) {
    setIsLoading(false);
    return;
  }

  setAuthToken(token);

  (async () => {
    try {
      console.log(' Tentative de rcupration du profil...');
      const data = await apiGet('/api/auth/me');
      console.log(' Rponse me:', data);

      if (data.success && data.data) {
        console.log(' Profil rcupr avec succs:', data.data);
        setUser(mapApiUserToFront(data.data));
      } else {
        console.error(' Erreur dans la rponse me:', data?.message);
        setAuthToken(null);
        removeStorage('auth_token'); // Utiliser removeStorage
      }
    } catch (error) {
      console.error(' Erreur lors du chargement du profil:', error);
      setAuthToken(null);
      removeStorage('auth_token'); // Utiliser removeStorage
      setUser(null);
    } finally {
      setIsLoading(false);
    }
  })();
}, []);


  const login = async (email: string, password: string): Promise<void> => {
  setIsLoading(true);
  try {
    const data = await apiJson('/api/auth/login', 'POST', { email, password });
    
    if (!data.success) {
      throw new Error(data.message || 'Erreur de connexion');
    }
    
    let authToken = null;
    if (data.data?.token) {
      authToken = data.data.token;
    } else if (data.token) {
      authToken = data.token;
    } else {
      authToken = 'demo-token';
      console.log(' Token de dmo enregistr (fallback)');
    }

    if (authToken) {
      setAuthToken(authToken);
      console.log(' Token enregistr pour le port', window.location.port);
    }

      if (data.data?.user) {
        setUser(mapApiUserToFront(data.data.user));
      } else if (data.user) {
        setUser(mapApiUserToFront(data.user));
      } else if (data.data) {
        setUser(mapApiUserToFront(data.data));
      } else {
        try {
          const meData = await apiGet('/api/auth/me');
          if (meData.success) {
            setUser(mapApiUserToFront(meData.data));
            console.log(' Utilisateur connect via me:', meData.data);
          }
        } catch (meError) {
          console.warn(' Impossible de charger le profil via me:', meError);
        }
      }
      
      toast.success('Connexion russie !');
    } catch (error: any) {
    setAuthToken(null);
    removeStorage('auth_token'); // Utiliser removeStorage
    toast.error(error.message || 'Erreur lors de la connexion');
    throw error;
  } finally {
    setIsLoading(false);
  }
};

  const register = async (userData: RegisterData | FormData): Promise<void> => {
    setIsLoading(true);
    
    try {
      let profileImage: File | null | undefined;
      let registerData: Omit<RegisterData, 'profileImage' | 'confirmPassword'>;
      
      // Grer  la fois les FormData et les objets simples
      if (userData instanceof FormData) {
        // Extraire l'image si elle existe
        profileImage = userData.get('profileImage') as File | null || undefined;
        
        // Crer un objet avec les donnes du formulaire
        const formDataObj: Record<string, any> = {};
        userData.forEach((value, key) => {
          if (key !== 'profileImage') {
            formDataObj[key] = value;
          }
        });
        
        // Convertir les champs numriques
        if (formDataObj.budget) {
          formDataObj.budget = Number(formDataObj.budget) || null;
        }
        
        registerData = formDataObj as Omit<RegisterData, 'profileImage' | 'confirmPassword'>;
      } else {
        // Grer l'objet simple
        const { profileImage: img, confirmPassword, ...rest } = userData;
        profileImage = img;
        
        // Convertir le budget en nombre si ncessaire
        if (userData.budget !== undefined && userData.budget !== null) {
          userData.budget = typeof userData.budget === 'string' && userData.budget !== '' ? 
            Number(userData.budget) : 
            (userData.budget as number | undefined);
        } else {
          // Si le budget est null ou undefined, on le supprime pour viter les erreurs de typage
          delete userData.budget;
        }
        
        const budgetValue = userData.budget;
        
        registerData = {
          ...rest,
          budget: budgetValue,
        };
      }
      
      // Envoyer les donnes d'inscription
      const data = await apiJson('/api/auth/register', 'POST', registerData);
      
      if (!data.success) {
        throw new Error(data.message || 'Erreur lors de l\'inscription');
      }
      
      // Si l'inscription russit et qu'il y a une image de profil, on l'upload maintenant
      if (profileImage && data.data?.token) {
        try {
          // Dfinir le token d'authentification pour les requtes suivantes
          setAuthToken(data.data.token);
          
          const form = new FormData();
          form.append('image', profileImage);
          
          const uploadData = await apiUpload('/api/upload/image', form);
          
          if (uploadData.success && uploadData.data?.publicId) {
            // Stocker uniquement l'ID public dans la base de donnes
            await apiJson('/api/users/me', 'PUT', {
              avatar: uploadData.data.publicId
            });
            
            // Mettre  jour l'utilisateur avec le nouvel avatar
            if (data.data.user) {
              data.data.user.avatar = uploadData.data.publicId;
              setUser(mapApiUserToFront(data.data.user));
            }
          }
        } catch (uploadError) {
          console.error('Erreur lors de l\'upload de l\'image de profil:', uploadError);
          // Ne pas chouer l'inscription  cause de l'chec de l'upload de l'image
        } finally {
          // Se dconnecter aprs l'upload
          setAuthToken(null);
        }
      }
      
      // Afficher un message de succs
      toast.success('Inscription russie ! Vous pouvez maintenant vous connecter.');
      
    } catch (error: any) {
      console.error('Erreur lors de l\'inscription:', error);
      toast.error(error.message || 'Erreur lors de l\'inscription');
      throw error;
    } finally {
      setIsLoading(false);
    }
  };
  const logout = async () => {
  try {
    await apiJson('/api/auth/logout', 'POST');
  } catch (error) {
    console.error('Erreur lors de la dconnexion:', error);
  } finally {
    setUser(null);
    setAuthToken(null);
    clearStorage(); // Utiliser clearStorage
    toast.success('Dconnexion russie');
  }
};

  const updateProfile = async (profileData: Partial<User>): Promise<void> => {
    try {
      const data = await apiJson('/api/users/me', 'PUT', profileData);
      
      if (!data.success) {
        throw new Error(data.message || 'Erreur lors de la mise  jour');
      }
      
      setUser(prev => prev ? { ...prev, ...mapApiUserToFront(data.data) } : mapApiUserToFront(data.data));
      toast.success('Profil mis  jour avec succs !');
    } catch (error: any) {
      toast.error(error.message || 'Erreur lors de la mise  jour du profil');
      throw error;
    }
  };

  // Dans la fonction verifyCIN de AuthContext.tsx
  const verifyCIN = async (verificationData: CINVerificationData): Promise<void> => {
  setIsLoading(true);
  try {
    console.log(' Dbut de la vrification CIN simple...');

    // 1. Vrification simple
    const verificationResult = await cinVerificationService.verifyCIN(
      verificationData.cinNumber,
      verificationData.cinRectoImage,
      verificationData.cinVersoImage
    );

    console.log(' Rsultat vrification:', verificationResult);

    if (!verificationResult.success) {
      const errorMessage = verificationResult.message || 'Validation CIN locale choue';
      throw new Error(errorMessage);
    }

    // 2. Upload images first (if needed)
    
    // Upload recto image
    let cinRectoImagePath = null;
    if (verificationData.cinRectoImage) {
      const rectoFormData = new FormData();
      rectoFormData.append('image', verificationData.cinRectoImage);
      const rectoData = await apiUpload('/api/upload/image', rectoFormData);
      if (!rectoData.success) {
        throw new Error(rectoData.message || 'Upload recto chou');
      }
      cinRectoImagePath = rectoData.data.path;
    }

    // Upload verso image
    let cinVersoImagePath = null;
    if (verificationData.cinVersoImage) {
      const versoFormData = new FormData();
      versoFormData.append('image', verificationData.cinVersoImage);
      const versoData = await apiUpload('/api/upload/image', versoFormData);
      if (!versoData.success) {
        throw new Error(versoData.message || 'Upload verso chou');
      }
      cinVersoImagePath = versoData.data.path;
    }

    // 3. Send CIN verification data
    const backendData = await apiJson('/api/auth/verify_cin', 'POST', {
      cinNumber: verificationData.cinNumber,
      cinRectoImagePath,
      cinVersoImagePath,
      cinData: (verificationResult as any).cinData || null
    });

    if (!backendData.success) {
      throw new Error(backendData.message || 'Erreur lors de la vrification CIN');
    }

    // 4. Recharger l'utilisateur depuis le serveur pour avoir les donnes  jour
    try {
      const meData = await apiGet('/api/auth/me');
      if (meData.success && meData.data) {
        setUser(mapApiUserToFront(meData.data));
        console.log(' Utilisateur recharg aprs vrification CIN:', meData.data);
      }
    } catch (meError) {
      console.warn(' Impossible de recharger le profil:', meError);
      // Mettre  jour localement en fallback
      if (user) {
        const updatedUser = {
          ...user,
          isVerified: false,
          cinVerified: false,
          cinPending: true,
          cinNumber: verificationData.cinNumber
        };
        setUser(updatedUser);
      }
    }
    
    toast.success('Vrification CIN soumise avec succs ! En attente de validation par un administrateur.');

  } catch (error: any) {
    console.error(' Erreur vrification CIN:', error);
    toast.error(error.message || 'Erreur lors de la vrification CIN');
    throw error;
  } finally {
    setIsLoading(false);
  }
};

  const mapApiUserToFront = (apiUser: any): User => {
    // Logique amliore pour dterminer cinVerified
    let isCinVerified = false;
    
    // Vrifier toutes les possibilits de champs pour cinVerified
    if (apiUser.cinVerified !== undefined) {
      isCinVerified = Boolean(apiUser.cinVerified);
    } else if (apiUser.cin_verified !== undefined) {
      isCinVerified = Boolean(apiUser.cin_verified);
    } else if (apiUser.cin_verified_at) {
      // Si une date de vrification existe, considrer comme vrifi
      isCinVerified = true;
    }
    
    // Dterminer si la CIN est en attente
    const hasCinImages = !!(apiUser.cin_recto_image_path || apiUser.cin_verso_image_path);
    const hasPendingStatus = apiUser.cinPending !== undefined ? Boolean(apiUser.cinPending) : false;
    const cinPending = (hasCinImages && !isCinVerified) || hasPendingStatus;

    return {
      id: String(apiUser.id),
      email: apiUser.email,
      firstName: apiUser.firstName || apiUser.first_name || '',
      lastName: apiUser.lastName || apiUser.last_name || '',
      phone: apiUser.phone || '',
      userType: apiUser.userType || apiUser.user_type,
      university: apiUser.university || undefined,
      studyLevel: apiUser.studyLevel || apiUser.study_level || undefined,
      budget: apiUser.budget !== null && apiUser.budget !== undefined ? Number(apiUser.budget) : undefined,
      preferences: Array.isArray(apiUser.preferences) ? apiUser.preferences : [],
      bio: apiUser.bio || '',
      avatar: apiUser.avatar || undefined,
      isVerified: Boolean(apiUser.isVerified || apiUser.is_verified || false),
      cinVerified: isCinVerified,
      cinPending: cinPending,
      cinNumber: apiUser.cinNumber || apiUser.cin_number || undefined,
      cinData: apiUser.cinData ? (typeof apiUser.cinData === 'string' ? JSON.parse(apiUser.cinData) : apiUser.cinData) : undefined,
      cin_verification_requested_at: apiUser.cin_verification_requested_at || undefined,
      cin_verified_at: apiUser.cin_verified_at || undefined,
      accountActivationDeadline: apiUser.account_activation_deadline ? new Date(apiUser.account_activation_deadline) : undefined,
      createdAt: apiUser.createdAt ? new Date(apiUser.createdAt) : (apiUser.created_at ? new Date(apiUser.created_at) : new Date()),
      updatedAt: apiUser.updatedAt ? new Date(apiUser.updatedAt) : (apiUser.updated_at ? new Date(apiUser.updated_at) : new Date()),
    };
  };

  const value: AuthContextType = {
    user,
    setUser,
    login,
    register,
    logout,
    updateProfile,
    isLoading,
    verifyCIN
  };

  return (
    <AuthContext.Provider value={value}>
      {children}
    </AuthContext.Provider>
  );
};